<template>
    <div v-if="!success" @click="handle">

        <div class="status flex" v-if="loading">
            <Loading/>
        </div>
        
        <div class="status flex" v-if="error">
            <Error/>
        </div>

        <div class="status flex" v-if="empty">
            <Empty/>
        </div>
    </div>
</template>

<script>
import Loading from './Loading.vue'
import Error   from './Error.vue'
import Empty   from './Empty.vue'
export default {
    name: 'StatusLayout',
    components:{
        Loading,
        Error,
        Empty
    },
    data(){
        return {
            loading:false,
            success:false,
            error  :false,
            empty  :false
        }
    },
    methods:{
        handle(){
            if(this.error){
                location.reload()
            }
        }
    }
}
</script>

<style scoped>
    .status{
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background: white;
    }

</style>

<style>
    .status img{
        width: 150px;
        margin-bottom: 10px;
    }
</style>