<template>
    <div class="flex" v-if="type.length != 0">
        <span class="tag" @click="show=!show">{{type[currentIndex].tag}}</span>
        <ul class="type" v-if="show">
            <li v-for="item in type" :key="item.id" @click="currentIndex = item.id-1;show=false;getMarkdown()">
                {{item.tag}}
            </li>
        </ul>
        <div id="md" for="html-export">
            <div class="markdown-preview flex" id="show" v-html="html"></div>
        </div>
    </div>
    <img src="/static/img/arrow-up.svg" class="btn-up" @click="this.$toTop" v-if="html != ''">
    <StatusLayout ref="sl"/>
</template>

<script>
import StatusLayout from '../components/StatusLayout.vue'
export default {
    name: 'Question',
    components:{
        StatusLayout
    },
    data(){
        return {
            currentIndex:0,
            type:[],
            show:false,
            html:'',
        }
    },
    methods:{

        getType(){
            this.$refs.sl.loading = true
            this.$http.get("/static/data/question.json?q="+new Date().getTime())
                .then(res => {
                    this.type = res.data.data
                    this.getMarkdown()
                })
        },

        getMarkdown(){
            this.$refs.sl.loading = true
            if(this.type[this.currentIndex].html == null){
                this.$http.get(`https://md.jqwong.cn/question/${this.currentIndex+1}.md?q=`+new Date().getTime())
                .then(res => {
                    this.$refs.sl.loading = false
                    this.type[this.currentIndex].html = marked(res.data)
                    this.html = this.type[this.currentIndex].html
                })
                .catch(error => {
                    console.log(error)
                    this.$refs.sl.error = true
                })
            }
            else{
                this.html = this.type[this.currentIndex].html
                this.$refs.sl.loading = false
            }
        }
    },
    mounted(){
        if(this.type.length == 0){
            this.getType()
        }
        else{
            this.getMarkdown()
        }
    }
}
</script>

<style scoped>
    .tag{
        background: cornflowerblue;
        display: inline-block;
        margin: 10px 0 0 0;
        padding: 10px;
        color: white;
        cursor: pointer;
        text-align: center;
    }

    .type{
        list-style: none;
        text-align: center;
        background: #dadada;
    }

    .type li{
        padding: 10px 0 10px 0;
        cursor: pointer;
    }
</style>